@import url(https://fonts.googleapis.com/css2?family=Play:wght@400;700&display=swap);
*,::after,::before{margin:0;padding:0;box-sizing:border-box;outline:none;border:none;color:#105652}html{font-size:0.75rem}@media (min-width: 576px){html{font-size:0.812rem}}@media (min-width: 768px){html{font-size:0.937rem}}body{background:#2bffbf;display:grid;place-items:center;font-family:"Play", sans-serif;min-height:100vh}svg{width:100%;height:auto}button{background-color:#f5fdb0;color:#b91646;font-size:1.5rem;display:inline-block;padding:1rem 1.3rem;border-radius:0.65rem;cursor:pointer;transition:0.3s}button:hover{box-shadow:0rem 0rem 1rem #2bffbf}

.editor{background-color:#bcffb9;border-radius:.65rem;padding:3rem 2rem;display:flex;flex-direction:column;align-items:center;grid-gap:1rem;gap:1rem}.editor__bg{width:100%;height:auto}.editor__options{display:flex;grid-column-gap:2rem;-webkit-column-gap:2rem;column-gap:2rem}.editor__input{display:flex;flex-direction:column;justify-content:center;align-items:center;grid-gap:1rem;gap:1rem}.editor__input input{border-radius:.65rem;width:5rem;height:5rem;text-align:center;background-color:#f5fdb0;font-size:1.3rem;color:#b91646;transition:0.3s}.editor__input input:hover{box-shadow:0rem 0rem 1rem #2bffbf}.editor__input span{color:#b91646;font-size:1.4rem}

.drawing-panel{display:flex;flex-direction:column;align-items:center;grid-gap:1.2rem;gap:1.2rem}

.pixel{width:0.8rem;height:0.8rem}.pixel:hover{cursor:pointer}

.row{display:flex;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}

